.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

#contact {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;    
    height: 900px;
}
.contact-form-div {  
    
    display: flex;
    flex-direction: row;  
    margin-bottom: 20px;
    text-align: left;
    max-width: 100%;
    width: 100%;    
    justify-content: center;
    align-items: center;
    align-content: center;
    
}
.contact-svg {
    display: flex;
    width: 40%;    
     
}

.contact-svg-im {
    opacity: .6;
    height: 500px;
    
}

.contact-svg {
    width:50%;

}
.contact-form {   
    margin-top:20px;
    display: flex;
    flex-direction: column;    
    margin-bottom: 20px;   
    max-width: 100%;
   
    
}
.contact-form h3 {
    font-family: 'Montserrat';
    font-weight: 800;
    font-size: 30px;
    margin-bottom: 20px;    
}
 label {
     font-family: 'Montserrat';
     font-size: 10\5px;
     font-weight: 600;
 }

.names {
    display: flex;
    flex-direction: row;
    width: 100%;
}
.names .first-name, .last-name {
    display: flex;
    flex-direction: column;
}

.names input {
    width: 200px;
    height: 50px;
    background-color: none;
    margin-right: 50px;
    padding: 20px;
    
}

.contact {
    display: flex;
    flex-direction: column;    
    margin-top: 10px;
    
}

.contact .mail, .phone-number {
    display: flex;
    flex-direction: column;
}

.phone-number {
    margin-top: 10px;
}

.contact input {
    width: 450px;
    height: 50px;
    background-color: none;
    margin-right: 50px;
    padding:20px;
    
}
.textarea-input {
    display: flex;
    flex-direction: column;    
    margin-top: 10px;
}

.big-text {
    display: flex;
    flex-direction: column;    
    margin-top: 10px;
    
}

.big-text .service-text {
    display: flex;
    flex-direction: column;

}
textarea {
    background-color: transparent;
    color: rgb(14, 14, 14);
    border: 3px solid #000;
    font-size: 20px;  
    width: 450px;
    padding: 20px;

}
input {
    background-color: transparent;
    color: rgb(14, 14, 14);
    border: 3px solid #000;
    font-size: 20px;  
    font-family: 'Montserrat';
}

input:focus{
    background-color:none;
    outline-color: #000;
    border-color: #000;
    outline-width: 0ch;
}

.button-submit {
    background-color: #000000;
    color: #fff;
    border: none;
    text-align: center;  
    width: 150px;
    height: 40px;
    cursor: pointer;
    margin-top: 10px;
    margin-bottom: 10px;
    font-family: 'Montserrat';
    font-weight: 800;
    
}




