.error{
    display: flex;
    width: 100%;
   
    flex-direction: column;
    align-items: center;
    margin: 100px auto;
    background-color: #ffffff;
}
@-webkit-keyframes pre {
    0%   {width: 50px; height: 50px;}
    25%  {width: 100px; height: 100px;}
    50%  {width: 200px; height: 200px;}
    75%  {width: 250px; height: 250px;}
    100% {width: 300px; height: 300px;}
}
@keyframes pre {
    0%   {width: 50px; height: 50px;}
    25%  {width: 100px; height: 100px;}
    50%  {width: 200px; height: 200px;}
    75%  {width: 250px; height: 250px;}
    100% {width: 300px; height: 300px;}
}
.error-box{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    align-items: center;
}

.error-svg {
    width: 100%;
       
}

.error-svg-im {
    width: 500px;
    height: 500px;
    /*animation: pre  1s linear; */
}

.error-text {
    display: flex;
    flex-direction: column;
    align-content: center;
    text-align: center;
    margin-top: 0px;
}

.error-text h3  {
    margin: 0 0 12.5px;
    font-family: 'Montserrat';
    font-weight: bold;
    line-height: 1.2;
    overflow-wrap: break-word;
    word-wrap: break-word; 
    color: #3d4246;
}

.error-link {
    
    font-family: 'Montserrat';     
    font-weight: 200;  
    overflow-wrap: break-word;
    word-wrap: break-word; 
    color: #989898;
    display: flex;
    flex-direction: row;
    align-items: center;
    align-content: center;
    justify-content: center;
    justify-items: center;
    text-decoration: underline;
    
    
}
@-webkit-keyframes prep {
    0%   {margin-right: 2px;}
    25%  {margin-right: 6px;}
    50%  {margin-right: 8px;}
    75%  {margin-right: 10px;}
    100% {margin-right: 8px;}
}
@keyframes prep {
    0%   {margin-right: 2px;}
    25%  {margin-right: 6px;}
    50%  {margin-right: 8px;}
    75%  {margin-right: 10px;}
    100% {margin-right: 8px;}
}
.lefty {
    margin-right: 8px;
    -webkit-animation: prep infinite  1s linear;
            animation: prep infinite  1s linear; 
}
.error-link:hover {
    color: #6d6c6c;
}

